.hero-box {
  z-index: 998;
  padding-top: 130px;
  min-height: 100vh;
}
.hero-box,
.hero-box:before {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
}
.hero-box:before {
  content: "";
  background-color: rgba(0, 0, 0, 0.6);
}
.hero-box.box-profile {
  padding-top: unset;
}
.hero-box.box-profile:before {
  width: 0;
  height: 0;
}
.hero-box.box-profile > .blur {
  background-color: #fff;
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
}
.hero-box .box {
  background-color: #fff;
  border: 1px solid transparent;
  border-radius: 10px;
  padding: 0;
  margin-bottom: 50px;
}
.hero-box .box .box-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: 1px solid #eae8ff;
}
.hero-box .box .box-header.text {
  padding: 18px 20px;
  position: relative;
}
.hero-box .box .box-header.text a {
  position: absolute;
  left: 20px;
  font-size: 17px;
  font-weight: 600;
  cursor: pointer;
}
.hero-box .box .box-header.text span {
  margin: 0 auto;
  font-weight: 600;
}
.hero-box .box .box-header ul {
  margin: 0 auto;
  display: flex;
  height: 100%;
  padding: 0;
  justify-content: center;
  align-items: center;
}
.hero-box .box .box-header ul li {
  padding-top: 18px;
  height: 100%;
  margin-right: 40px;
  list-style: none;
  cursor: pointer;
  font-weight: 500;
}
.hero-box .box .box-header ul li:after {
  content: "";
  display: block;
  height: 4px;
  margin-top: 14px;
  background-color: transparent;
  border-top-right-radius: 6px;
  border-top-left-radius: 6px;
}
.hero-box .box .box-header ul li:last-of-type {
  margin-right: 0;
}
.hero-box .box .box-header ul li a {
  color: #aaacb6;
  transition: 0.2s ease-in-out;
}
.hero-box .box .box-header ul li.active:after {
  background-color: #563fe3;
}
.hero-box .box .box-header ul li.active a {
  color: #000;
}
.hero-box .box .box-content {
  padding: 21px 48px;
}
.hero-box .box .box-content input {
  width: 100%;
}
.hero-box .box .box-content a {
  margin-top: 20px;
  display: block;
  cursor: pointer;
  text-align: center;
}

