.game-card {
  background: #EAE8FF;
  width: 100%;
  /* Primary/Lavender sky */
  display: block;
  border: 1px solid #EAE8FF;
  box-sizing: border-box;
  box-shadow: 0px 6px 15px rgba(42, 27, 129, 0.05);
  border-radius: 10px;
  cursor: pointer;
}

.game-card.open {
	transform: rotateY(0);
	cursor: default;
	animation-name: flipInY;
	-webkit-backface-visibility: visible !important;
	backface-visibility: visible !important;
	animation-duration: .75s;
}

.game-card.close {
	transform: rotateY(0);
	cursor: default;
	animation-name: flipOutY;
	-webkit-backface-visibility: visible !important;
	backface-visibility: visible !important;
	animation-duration: .75s;
}

